@import url(https://fonts.googleapis.com/css2?family=Manrope&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Carattere&display=swap);
/* Google Font */

/* Body Styles  */
body {
    font-family: 'Manrope', sans-serif;
    min-height: 105vh;
}

.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}

/* Navbar Styles  */
nav {
    margin-bottom: 20px;
    border-bottom: 2px groove;
}

.navbar-brand {
    font-size: 1.8rem;
    font-weight: bold;
    text-align: center;
    margin-left: 20px;
}

.navbar-nav a {
    font-size: 1.3rem;
}

.navbar-nav a:hover {
   -webkit-transform: scale(1.3);
           transform: scale(1.3);
   transition: ease-out 0.2s;
   color: blueviolet;
   text-decoration: underline;
}

.navbar-nav a:active {
   -webkit-transform: scale(0.6);
           transform: scale(0.6);
   transition: ease-out 0.2s;
}

.mode i {
    font-size: 1.5rem;
    padding-top:10px;
}

.mode i:hover {
   color: rgb(137, 43, 226);
}

lord-icon {
    width: 40px;
    height: 40px;
}

/* Head Style */
.head {
    margin-bottom: 20px;
}

.head h1{
   font-family: 'Carattere', cursive;
   text-align: center;
   font-size: 4rem;
}

.head hr {
    max-width: 80%;
    margin: auto;
}

/* Home Style */
.main-left {
    padding: 20px;
    border: 3px solid #9900FF;
    border-radius: 10px;
    margin: 10px;
}

.main-left p {
    font-size: 1.2rem;
}
.main-right {
    padding: 20px;
    border: 3px solid #FF3300;
    border-radius: 10px;
    margin: 10px;
}

/* Resume Style */
.exp {
    border:3px solid #099FFF;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 20px;
}

.exp .title a {
    text-decoration: none;
    font-size: 2rem;
}

.exp .time {
    font-size: 1rem;
    font-weight: bold;
    background-color: #8bcef8;
    padding: 8px;
    border-radius: 10px;
}

.tasklist {
    list-style-position: inside;
    padding: 5px;
    border-left: solid 2px orangered;
    margin-top: 10px;
}

.task {
    font-size: 1.2rem;
    font-family: monospace;
}

.edu {
    border:3px solid #099FFF;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 20px;
}

.edu .title {
    font-size: 2rem;
}

.edu .time {
    font-size: 1rem;
    font-weight: bold;
    background-color: #8bcef8;
    padding: 8px;
    border-radius: 10px;
}

.edu .college {
    font-size: 1.6rem;
    font-family: monospace;
    list-style-position: inside;
    padding: 5px;
    border-left: solid 2px #9900FF;
    margin-top: 10px;
}

.edu .major {
    font-size: 1.2rem;
}









/* media query */
@media only screen and (max-width: 450px) {

    .navbar-brand {
        font-size: 1.5rem;
    }
}

@media only screen and (max-width:700px) {
    .edu {
        border: none;
    }

    .edu .title {
        font-size: 1.6rem;
        text-decoration: underline;
        -webkit-text-decoration-color: #099FFF;
                text-decoration-color: #099FFF;
    }

    .task {
        font-size: 1.2rem;
    }

    .exp {
        border: none;
    }

    .exp .title a {
        font-size: 1.6rem;
        text-decoration: underline;
        -webkit-text-decoration-color: #099FFF;
                text-decoration-color: #099FFF;
    }

}
